import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login/Login.vue'
import Home from '@/components/Home/Home'
import Test from '@/components/Home/Test'

Vue.use(VueRouter)

const routes = [
  {
    path: '',
    component: Login,
    meta:{
      title:'登录-云云说后台'
    }
  },
  {
    path: '/login',
    component: Login,
    meta:{
      title:'登录-云云说后台'
    }
  },
  {
    path: '/home',
    component: Home,
    meta:{
      title:'主页-云云说后台'
    }
  },
  {
    path: '/test',
    component: Test,
    meta:{
      title:'Test-云云说后台'
    }
  }

]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  //  next() direct let pass
  //  next('path') inject

  const tokenStr = window.sessionStorage.getItem('myToken')

  if (to.path === '/login') {
    if (!tokenStr) {
      document.title = to.meta.title
      return next()
    }
    return next(false)
  }

  if (!tokenStr) {
    document.title = '登录-云云说后台'
    return next('/login')
  }

  document.title = to.meta.title
  next()
})


export default router
