                <el-table-column prop="id" label="广告ID" show-overflow-tooltip></el-table-column>
                <el-table-column prop="name" label="广告名" show-overflow-tooltip></el-table-column>
                <el-table-column prop="scan_count" label="总扫码数" show-overflow-tooltip></el-table-column>
                <el-table-column prop="display_count" label="当前设备数" show-overflow-tooltip></el-table-column>
                <el-table-column prop="last_scan_time" label="最近扫码时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="last_24h_scan_time" label="24小时扫码数" show-overflow-tooltip></el-table-column>


              </el-table>

