<script>
import router from "@/router";
const axios = require('axios').default;

export default {
  name: 'Home',
  props: {
    msg: String
  },
  created () {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData()
  },
  data() {
    return {
      mouse_hover_device_table_row_id: 'NA',
      mouse_hover_device_table_column_name: 'NA',
      mouse_hover_site_table_row_id: 'NA',
      mouse_hover_site_table_column_name: 'NA',
      showTable: true,
      showChangeAdDialog: false,
      showChangeSiteDiaglog: false,
      showAdChangeButton: true,
      showSiteManagementButtons: false,
      ad_table_loading: false,
      activeName: 'tab_devices',
      headers :{
        'Authorization': 'Bearer ' + window.sessionStorage.getItem('myToken')
        // // 'Access-Control-Allow-Origin' : '*',
        // 'Access-Control-Allow-Methods' : 'DELETE, POST, GET, OPTIONS',
        // 'Access-Control-Allow-Headers' : 'Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With'
      },
      tableData: [],
      labelMacs: [],
      sites_data_without_placeholder: [],
      sites_data_with_placeholder: [
        {
          index: -1,
          id: 'NULL',
          name: '不分配'
        }
      ],
      ad_data_with_placeholder:[
        { index: -1,
          name: '请选择广告'
        }
      ],
      ad_data_without_placeholder: [],
      loading:true,
      deviceSelection: [],
      siteSelection: [],
      param_for_querying_labels : {page : 1, size: 1000, storeUuid : '0108'},
      param_for_querying_gateways : {page : 1, size: 1000, storeUuid : '0108'},
      form: {
        ad_index : -1,
        site_index: -1
      },
      formLabelWidth: '120px'
    }
  },

  methods: {

    logout(){
      console.log('logout pressed')
      window.sessionStorage.removeItem('myToken');
      router.push('/login')
    },

    fetchData(){
      this.loading = true
      let self = this

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      axios.all([
        axios.get('api/device/esl/query',this.param_for_querying_labels, {
          headers: this.headers,
        }),

        axios.get('api/gateway/esl/query', {
         headers: this.headers,
        })

      ]).then(checkFailed(([response1, response2]) => {
        self.fetchLabelDataSucceeded(response1)
        self.fetchGatewayDataSucceeded(response2)
      })).catch(axios.spread((error1, error2) => {
            self.fetchLabelDataFailed(error1)
            self.fetchGatewayDataFailed(error2)
          }
      ))

      this.fetchAdList()
      this.fetchSiteDetails()

    },

    // Handling label data
    fetchLabelDataSucceeded(response){
      console.log('label fetching succeeded')
      console.log(response.data)

      let labelInfoList = response.data.body.list
      console.log(labelInfoList.length + ' labels found')
      let self = this

      for (let i = 0; i < labelInfoList.length; i++) {

        let labelInfo = labelInfoList[i]
        let mac = labelInfo.mac
        this.labelMacs.push(mac)
        let batteryLevel = labelInfo.battery
        let screenSizeText = labelInfo.screenSize
        let connection_status = '离线'
        let gateway_mac = ''
        if(labelInfo.correspond != null){
            gateway_mac = labelInfo.correspond.gatewayMac
        }

        if(labelInfo.heartbeat != '0'){
          connection_status = '在线'
        }

        this.tableData.push({
          id : mac,
          battery_level: batteryLevel,
          screen_size: this.decodeScreenSize(screenSizeText),
          device_type:'屏幕',
          connection_status: connection_status,
          gateway_mac: gateway_mac,
          ad_name : ''
        })
      }

      self.fetchGoodsBinding()
      self.fetchLabelBackendData()
      // self.fetchLabelTemplateBinding()

      this.loading = false
    },

    fetchLabelDataFailed(error){
      if(error == null) return
      this.loading = false
      this.$message.error('设备信息拉取失败');
      console.log('label fetching failed')
    },

    fetchSiteDetails(){
      let self = this

      axios.get('/lp/get-site-list', {
        // baseURL: 'http://81.68.165.124:8080/'
        // baseURL: 'http://192.168.1.127:8080/'
      }).then(response => {
        console.log(response)
        const site_list = response.data

        if(response.status != 200) return

        self.sites_data_without_placeholder = []
        self.sites_data_with_placeholder = [
          {
            index: -1,
            id: 'NULL',
            name: '不分配'
          }
        ]

        for (let i = 0; i < site_list.length; i++) {
          const site = site_list[i]

          if(site.siteId == 'NULL'){
            continue
          }

          self.sites_data_without_placeholder.push({
            index: self.sites_data_without_placeholder.length,
            id: site.siteId,
            name: site.name,
            device_count : site.deviceCount,
            scan_count: site.scanCount
          })

          self.sites_data_with_placeholder.push({
            index: self.sites_data_with_placeholder.length-1,
            name: site.name
          })

        }
      })
    },

    decodeScreenSize(screenSizeText){
      if(screenSizeText === '000c'){
        return 4.2
      }else if(screenSizeText === '000f'){
        return 5.83
      }
      return 'not defined'
    },

    //Handling gateway data
    fetchGatewayDataSucceeded(response){
      console.log('gateway fetching succeeded')
      console.log(response.data)
      let gatewayInfoList = response.data.list
      console.log(gatewayInfoList.length + ' gateways found')

      for (let i = 0; i < gatewayInfoList.length; i++) {
        let gatewayInfo = gatewayInfoList[i]
        let mac = gatewayInfo.mac
        this.tableData.push({
          id : mac,
          connection_status: gatewayInfo.mode,
          device_type:'网关',
          screen_size: 'N/A',
          battery_level: 'N/A'
        })
      }

      this.loading = false
    },

    fetchGatewayDataFailed(error){
      if(error == null) return
      this.loading = false
      this.$message.error('网关信息拉取失败');
      console.log('gateway fetching failed')
    },

    fetchAdList(){
      let self = this
      axios.get('/lp/get-ad-list', {
        // baseURL: 'http://81.68.165.124:8080/'
        // baseURL: 'http://192.168.1.127:8080/'
      }).then(response => {
        console.log(response)
        if(response.status != 200) return

        self.ad_data_with_placeholder = [
          { index: -1,
            name: '请选择广告'
          }
        ]

        self.ad_data_without_placeholder = []

        for (let i = 0; i < response.data.length; i++) {
          const ad_data = response.data[i]
          let ad_name = ad_data.name
          let ad_id = ad_data.landingPageId
          let large_demo_id = ad_data.largeDemoId
          let small_demo_id = ad_data.smallDemoId

          self.ad_data_with_placeholder.push({
            index: self.ad_data_with_placeholder.length-1,
            name: ad_name
          })

          self.ad_data_without_placeholder.push({
            index: self.ad_data_without_placeholder.length,
            id: ad_id,
            name: ad_name,
            small_demo_id: small_demo_id,
            large_demo_id: large_demo_id,
            temp_display_count: 0,
            display_count: 0
          })

        }
      })
    },

    fetchGoodsBinding(){
      let self = this
      console.log('fetch goods binding called')
      let goods_requests = []

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      for (let i = 0; i < this.labelMacs.length; i++) {
        goods_requests.push(axios.get('api/device/esl/query-binding', {
          headers: this.headers,
          params: {mac: this.labelMacs[i]}
        }))
      }

      axios.all(goods_requests).then(checkFailed((responses) => {
        console.log(responses)
        for (let i = 0; i < self.ad_data_without_placeholder.length; i++) {
            const ad_data = self.ad_data_without_placeholder[i]
            ad_data.temp_display_count = 0;
        }
        for (let i = 0; i < responses.length ; i++) {
          let response = responses[i]
          if(response.data.status!=200)continue
          console.log(response)

          let good_barcode = response.data.body.id
          let good_name = response.data.body.label4
          let url = response.data.body.qrcode
          let ad_id = url.substring(url.length-8, url.length)
          let thisMac = response.request.responseURL.split('=')[1]

          console.log('good result successfully fetched for ' + thisMac)

          for (let j = 0; j < self.tableData.length; j++) {
            const labelData = self.tableData[j]
            if (labelData.id == thisMac) {
              console.log(good_barcode.substring(0, good_barcode.length-12))
              // labelData.good_barcode_without_mac = good_barcode.substring(0, good_barcode.length-12)
              // labelData.good_barcode = good_barcode
              labelData.good_name = good_name
              labelData.ad_name = good_name
              labelData.good_info = response.data.body
              labelData.ad_id = ad_id
              // let found_matching_ad = false
              for (let k = 0; k < self.ad_data_without_placeholder.length; k++) {
                const adData = self.ad_data_without_placeholder[k]
                if(adData.id == labelData.ad_id){
                  adData.temp_display_count++
                  break
                }
              }
              //
              // if(!found_matching_ad){
              //   labelData.ad_name = good_name
              // }

              break
            }
          }

        }

        for (let i = 0; i < self.ad_data_without_placeholder.length; i++) {
          const ad_data = self.ad_data_without_placeholder[i]
          ad_data.display_count = ad_data.temp_display_count
          ad_data.temp_display_count = 0
        }

        self.reloadTable()
        self.fetchActionCount()

      })).catch(axios.spread((errors) => {
            console.log(errors)
          }
      ))
    },

    fetchLabelBackendData(){
      let self = this
      console.log('fetchLabelBackendData called')
      let requests = []

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      for (let i = 0; i < self.tableData.length; i++) {
        let labelData = self.tableData[i]
        let mac = labelData.id

        if(labelData.device_type == '网关'){
          continue
        }

        requests.push(axios.get('/lp/get-label-detail', {
          params: {
            mac: mac
          }
          // baseURL: 'http://81.68.165.124:8080/'
          // baseURL: 'http://192.168.1.127:8080/'
        }))

        axios.all(requests).then(checkFailed((responses) => {
          console.log(responses)
          for (let i = 0; i < responses.length ; i++) {
            const response = responses[i]

            console.log(response)
            const label_name = response.data.name
            const label_site_name = response.data.siteName
            const label_site_id = response.data.siteId
            const label_mac = response.data.mac

            for (let j = 0; j < self.tableData.length; j++) {
              const labelData = self.tableData[j]
              if(labelData.id == label_mac){
                labelData.name = label_name
                labelData.site_name = label_site_name
                labelData.site_id = label_site_id
                continue
              }
            }
          }

          self.reloadTable()

        })).catch(axios.spread((errors) => {
              console.log(errors)
            }
        ))

      }
    },

    fetchActionCount(){
      let self = this
      console.log('fetch action count called')
      let requests = []

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      for (let i = 0; i < self.tableData.length; i++) {
          let labelData = self.tableData[i]
          let mac = labelData.id
          let ad_name = labelData.ad_name
          let ad_id = ''

          for (let j = 0; j < self.ad_data_without_placeholder.length; j++) {
              let adData = self.ad_data_without_placeholder[j]
              if(ad_name == adData.name){
                ad_id = adData.id
                break
              }
          }

          if(ad_id == '') continue

          requests.push(axios.get('/lp/get-action-count', {
            params: {
              adId: ad_id,
              mac: mac
            }
            // baseURL: 'http://81.68.165.124:8080/'
            // baseURL: 'http://192.168.1.127:8080/'
          }))

          axios.all(requests).then(checkFailed((responses) => {
            console.log(responses)
            for (let i = 0; i < responses.length ; i++) {
              let response = responses[i]

              console.log(response)

              let thisMac = response.request.responseURL.split('mac=')[1]
              thisMac = thisMac.split("&")[0]

              console.log(thisMac)

              for (let j = 0; j < self.tableData.length; j++) {
                  const labelData = self.tableData[j]
                  if(labelData.id == thisMac){
                      labelData.action_count = response.data
                  }
              }

            }

            self.reloadTable()

          })).catch(axios.spread((errors) => {
                console.log(errors)
              }
          ))

      }


    },

    // fetchLabelTemplateBinding(){
    //   let self = this
    //   let templates_requests = []
    //
    //   function checkFailed (then) {
    //     return function (responses) {
    //       const someFailed = responses.some(response => response.error)
    //
    //       if (someFailed) {
    //         throw responses
    //       }
    //
    //       return then(responses)
    //     }
    //   }
    //
    //   for (let i = 0; i < this.labelMacs.length; i++) {
    //     let thisMac = this.labelMacs[i]
    //     templates_requests.push(axios.post('/V2/pub/binding/query', {mac: thisMac}, {
    //       headers: this.headers,
    //       params: {mac: thisMac},
    //     }))
    //   }
    //
    //   axios.all(templates_requests).then(checkFailed((responses) => {
    //     console.log(responses)
    //     for (let i = 0; i < responses.length ; i++) {
    //       let response = responses[i]
    //       console.log(response)
    //       if(response.data.status == 431) continue
    //       let template_id = response.data.body.templets[0].demoId
    //       let template_name = response.data.body.templets[0].demoName
    //       let thisMac = response.request.responseURL.split('=')[1]
    //       console.log(thisMac)
    //
    //       for (let j = 0; j < self.tableData.length; j++) {
    //         const labelData = self.tableData[j]
    //         if (labelData.id == thisMac){
    //           labelData.demo_id = template_id
    //           labelData.demo_name = template_name
    //           break
    //         }
    //       }
    //       self.reloadTable()
    //     }
    //
    //   })).catch(axios.spread((errors) => {
    //         console.log(errors)
    //       }
    //   ))
    // },

    fetchAdDetails(){
      this.ad_table_loading = true
      let adDetailRequests = []

      for (let i = 0; i < this.ad_data_without_placeholder.length; i++) {
        adDetailRequests.push(axios.get('/lp/get-ad-detail', {
          params: {
            adId: this.ad_data_without_placeholder[i].id
          }
          // baseURL: 'http://81.68.165.124:8080/'
          // baseURL: 'http://192.168.1.127:8080/'
        }))
      }

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      let self = this

      axios.all(adDetailRequests).then(checkFailed((responses) => {
        console.log(responses)

        for (let i = 0; i < responses.length; i++) {
          const response = responses[i]
          const ad_id = response.data.adId
          for (let j = 0; j < self.ad_data_without_placeholder.length; j++) {
              const ad_data = self.ad_data_without_placeholder[j]
              if(ad_data.id == ad_id){
                ad_data.scan_count = response.data.scanCount
                ad_data.last_24h_scan_time = response.data.scanCount24H
                ad_data.last_scan_time = response.data.lastScanTime
                break
              }
          }

        }

        self.ad_table_loading = false
        this.reloadTable()
      })).catch(axios.spread((errors) => {
            console.log(errors)
          }
      ))

    },

    changeAdClicked(){
      console.log('changeAdClicked')
      console.log('current selection: ' , this.deviceSelection)

      if(this.deviceSelection.length == 0){
        this.$message.error('请至少选择一个屏幕');
      }else{
        this.showChangeAdDialog = true
      }
    },

    cancelAdChange(){
      console.log('cancelAdChange')
      this.showChangeAdDialog = false
      this.form.ad_index = -1
    },

    confirmAdChange(){
      this.showChangeAdDialog = false

      if(this.form.ad_index == -1){
        return
      }

      let ad_info = this.ad_data_without_placeholder[this.form.ad_index]
      let update_requests = []

      for (let i = 0; i < this.deviceSelection.length; i++) {
        let mac = this.deviceSelection[i].id
        let demo_id = ad_info.small_demo_id
        if (this.deviceSelection[i].screen_size == 5.83) {
          demo_id = ad_info.large_demo_id
        }
        let url = 'http://www.yys1.top/lp/ld?mac=' + mac + '&adId=' + ad_info.id
        console.log(url)
        this.deviceSelection[i].good_info.qrcode = url
        this.deviceSelection[i].good_info.label4 = ad_info.name
        this.deviceSelection[i].good_info.label2 = url
        let update_params = {
          information: this.deviceSelection[i].good_info,
          mac: mac,
          demoId: demo_id
        }

        update_requests.push(axios.post('api/device/esl/update-binding', update_params, {
          headers: this.headers,
        }))
      }

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      let self = this
      axios.all(update_requests).then(checkFailed((responses) => {
          console.log(responses)
          // for (let i = 0; i < responses.length; i++) {
          //   let response = responses[i]
          //   if(response.request.data.status != 200)continue
          //   let thisMac = JSON.parse(response.config.data).mac
          //   for (let j = 0; j < self.tableData.length; j++) {
          //       let data = self.tableData[j]
          //       if(data.id == thisMac){
          //         let demo_id = ad_info.small_demo_id
          //         if (this.multipleSelection[i].screen_size == 5.83) {
          //           demo_id = ad_info.large_demo_id
          //         }
          //         data.demo_id = demo_id
          //         data.good_id = ad_info.barcode
          //       }
          //   }
          //
          // }
          loading.close()
          // self.fetchLabelTemplateBinding()
          self.fetchGoodsBinding()
          // this.fetchData()
      })).catch(axios.spread((errors) => {
            console.log(errors)
            loading.close()
          }
      ))

      this.form.ad_index = -1

    },

    changeSiteClicked(){
      if(this.deviceSelection.length == 0){
        this.$message.error('请至少选择一个屏幕');
      }else{
        this.showChangeSiteDiaglog = true
      }
    },

    cancelSiteChange(){
      this.showChangeSiteDiaglog = false
      this.form.ad_index = -1
    },

    confirmSiteChange(){
      this.showChangeSiteDiaglog = false

      let site_id = 'NULL'
      if(this.form.site_index != -1){
        site_id = this.sites_data_without_placeholder[this.form.site_index].id
      }

      let update_requests = []

      for (let i = 0; i < this.deviceSelection.length; i++) {

        update_requests.push(axios.get('/lp/device-change-site', {
          // baseURL: 'http://81.68.165.124:8080/',
          // baseURL: 'http://192.168.1.127:8080/',
          params: {
            site_id: site_id,
            mac: this.deviceSelection[i].id
          }
        }))
      }

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      let self = this
      axios.all(update_requests).then(checkFailed((responses) => {
        console.log(responses)
        self.fetchLabelBackendData()
      })).catch(axios.spread((errors) => {
            console.log(errors)
        self.fetchLabelBackendData()
          }
      ))

      this.form.ad_index = -1

    },



    addSiteClicked(){
      let self = this
      this.$prompt('新站点名称', '添加站点', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(({ value }) => {

        axios.get('/lp/create-new-site', {
          // baseURL: 'http://81.68.165.124:8080/',
          // baseURL: 'http://192.168.1.127:8080/',
          params: {
            site_name: value,
          }
        }).then(response => {

          if(response.data){
            console.log(response)
            self.fetchSiteDetails()
            this.$message({
              type: 'success',
              message: '修改成功'
            });
          }
        })
      })
    },

    deleteSiteClicked(){

      const self = this

      if(this.siteSelection.length == 0){
        this.$message.error('请至少选择一个屏幕');
        return
      }

      this.$confirm('删除站点后，该站点内的设备将处于未分配状态, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {

        let requests = []

        function checkFailed (then) {
          return function (responses) {
            const someFailed = responses.some(response => response.error)

            if (someFailed) {
              throw responses
            }

            return then(responses)
          }
        }

        for (let i = 0; i < this.siteSelection.length; i++) {
          const site = this.siteSelection[i]
          const site_id = site.id

          requests.push(axios.get('/lp/delete-site', {
            // baseURL: 'http://81.68.165.124:8080/',
            // baseURL: 'http://192.168.1.127:8080/',
            params: {
              site_id: site_id
            }
          }))
        }

        axios.all(requests).then(checkFailed((responses) => {
          self.fetchSiteDetails()
        })).catch(axios.spread((errors) => {
              console.log(errors)
              self.fetchSiteDetails()
            }
        ))
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });


    },

    reloadTable(){
      this.showTable = false
      this.$nextTick(() =>{
        this.showTable = true
      })
    },

    deviceTableCellClicked(row, column, cell, event){
        const mac = row.id
        let self = this
        if(this.mouse_hover_device_table_row_id == row.id && this.mouse_hover_device_table_column_name == '名称'
            && row.device_type == '屏幕'){
          this.$prompt('新设备名称', '修改设备名称', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          }).then(({ value }) => {

            console.log(mac, value)

            axios.get('/lp/update-device-name', {
              // baseURL: 'http://81.68.165.124:8080/',
              // // baseURL: 'http://192.168.1.127:8080/',
              params: {
                mac: mac,
                newName: value,
              }
            }).then(response => {

              if(response.data){
                console.log(response)
                self.fetchLabelBackendData()
                this.$message({
                  type: 'success',
                  message: '修改成功'
                });
              }
            })
          })
        }
    },

    deviceTableCellEntered(row, column, cell, event){
      this.mouse_hover_device_table_row_id = row.id
      this.mouse_hover_device_table_column_name = column.label
    },

    deviceTableCellLeft(row, column, cell, event){
      this.mouse_hover_device_table_row_id = 'NA'
      this.mouse_hover_device_table_column_name = 'NA'
    },

    siteTableCellClicked(row, column, cell, event){
      const site_id = row.id
      let self = this
      if(this.mouse_hover_site_table_row_id == row.id && this.mouse_hover_site_table_column_name == '站点名称'){
        this.$prompt('新站点名称', '修改站点名称', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {

          axios.get('/lp/update-site-name', {
            // baseURL: 'http://81.68.165.124:8080/',
            // // baseURL: 'http://192.168.1.127:8080/',
            params: {
              site_id: site_id,
              name: value,
            }
          }).then(response => {

            if(response.data){
              console.log(response)
              self.fetchSiteDetails()
              this.$message({
                type: 'success',
                message: '修改成功'
              });
            }
          })
        })
      }
    },

    siteTableCellEntered(row, column, cell, event){
      this.mouse_hover_site_table_row_id = row.id
      this.mouse_hover_site_table_column_name = column.label
    },

    siteTableCellLeft(row, column, cell, event){
      this.mouse_hover_site_table_row_id = 'NA'
      this.mouse_hover_site_table_column_name = 'NA'
    },

    checkDeviceSelectable(row){
      return row.device_type != '网关' && row.connection_status == '在线'
    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    handleDeviceSelectionChange(val) {
      this.deviceSelection = val;
    },

    handleSiteSelectionChange(val){
      this.siteSelection = val;
    },

    handleTabChange(tab, event){
      console.log(tab)
      this.showAdChangeButton = this.activeName == 'tab_devices';
      this.showSiteManagementButtons = this.activeName == 'tab_sites';
      if(this.activeName == 'tab_devices'){
        this.fetchGoodsBinding()
      }else if(this.activeName == 'tab_ads_supervising'){
        this.fetchAdDetails()
      }else if(this.activeName == 'tab_sites'){
        this.fetchSiteDetails()
      }
    },

    rowStyle({row, rowIndex}) {
      // if (row.connection_status != '在线') {
      //   console.log(rowIndex)
      //   return {"background-color" : "#e9c8c3"}
      // }
      for (let i = 0; i < this.deviceSelection.length; i++) {
        if(row.id === this.deviceSelection[i].id){
          return 'selectedStyle'
        }
      }

    }
  }
}


</script>

